<template>
  <div>
    <default-dialog
      ref="dialog"
      title="Alterar senha"
      :btnDisabled="btnDisabled"
      @confirm="changePassword" 
    >
      <div class="conteudo">
        <input-field
          id="password"
          ref="password"
          label="Digite sua nova senha"
          type="password"
          :rules="passwordRules"
          @onKeyUp="onKeyUp" 
          @onKeyUpEnter="focusToNext('confirmPassword')" 
        />
        <input-field
          ref="confirmPassword"
          id="confirmPassword"
          label="Confirme a senha"
          type="password"
          :rules="passwordRules"
          @onKeyUp="onKeyUp" 
        />
      </div>
    </default-dialog>
    <overlay ref="overlay" />
  </div> 
</template>

<script>
import DefaultDialog from '@/components/globais/dialogs/default/Default'
import Overlay from '@/components/globais/overlay/Overlay'
import FormMixin from '@/mixins/FormMixin'
import InputField from '@/components/globais/form/input-field/InputField'
import OverlayMixin from '@/mixins/OverlayMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'

export default {
  name: 'ChangePassword',
  components: { DefaultDialog, InputField, Overlay },
  mixins: [ FormMixin, OverlayMixin, SnackbarMixin ],
  data: () => ({
    dialog: null,
    password: null,
    confirmPassword: null,
    passwordRules: [
      value => !!value || 'Informe a senha',
      value => value.length >= 6 || 'A senha deve ter no mnimo 6 caracteres'
    ]
  }),
  computed: {
    btnDisabled () {
      if (!this.password || !this.confirmPassword || this.password !== this.confirmPassword) return true
      else if (this.password.length < 6) return true
      return false
    }
  },
  methods: {
    changePassword () {
      this.showOverlay('Alterando a senha...')
      this.$axios.post(`auth/barber/change-password`, { password: this.password })
      .then(response => {
        if (response.data.codigo === 200) {
          const msg = 'Senha alterada com sucesso!'
          this.showSnackbar(msg)
          this.hideOverlaySuccess(msg)
          this.password = ''
          this.confirmPassword = ''
          this.dialog = false
        } else this.setErro(response.data.corpo)
      })
      .catch ((err) => {
        this.setErro('No foi possvel alterar a senha!')
        window.console.log(err)
      })
    },
    onKeyUp (value, id) {
      this.$data[id] = value
    },
    setErro (msg) {
      this.showSnackbarErro(msg)
      this.hideOverlayError(msg)
    },
    showDialog () {
      this.$refs.dialog.showDialog()
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../scss/theme.scss';

.dialog { background-color: #fff; color: $primaryDark; }
.dialog-title { font-size: 1.2rem; }
</style>